/*stiky*/
.stiky{
    position: fixed;
    left:0;
    top:0;
    width:100%;
    background: white;
    z-index:99;
}
/*topbar*/
.topbar{
    display: flex;
    justify-content: space-between;
    align-items: center;
    background:#D43C33;
    padding:17px 10px 17px;}
.topbar > svg {
    background:#D43C33;
    width:142px;
    height:25px;
    vertical-align: top;
}
.topbar >a{
    color: white;
    line-height:20px;
    padding:5px 10px;
    position:relative;
}
.topbar > a::after{
    content:'';
    position: absolute;
    left:0;
    top:0;
    width:calc(200% - 2px);
    height:calc(200% - 2px);
    border:1px solid white;
    border-radius:30px;
    transform:scale(0.5);
    transform-origin:0 0 ;
}
/*tabs*/
.tabs >ul {
    display: flex;
    border-bottom:1px solid #E6E6E6;
}

.tabs >ul>li{
    width:33.3%;
    text-align: center;

}
.tabs >ul>li>span{
    display: inline-block;
    line-height:17px;
    padding:11.5px 5px 10.5px;
    position: relative;
}
.tabs >ul>li.active>span{
    color: #D33A31;
}

.tabs >ul>li.active>span::after{
    content:'';
    position: absolute;
    left:0;
    top:-1px;
    width:100%;
    height:100%;
    border-bottom:2px solid #D33A31;
}
.page{
    display: none;
}
.page.active{
    display: block;
}
/*page1*/
.pages{
    background: #FCFCFD;
    padding-top:0.1px;
    margin-top:104px;
}
.page1{
    overflow: hidden;}
.songslist > h2{
    font-size:17px;
    line-height:20px;
    padding-left:9px;
    margin:20px 0 14px;
    position: relative;
}
.songslist > h2::after{
    content:'';
    position: absolute;
    top:1px;
    left:0;
    width:100%;
    height:80%;
    border-left:2px solid #D33A31;
}
.songslist >ul{
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
}
.songslist >ul > li{
    width:33%;
    padding-bottom:16px;
}

.songslist >ul > li  div{
    padding-top:100%;
    position: relative;
}
.songslist >ul > li img{
    position: absolute;
    top:0;
    left:0;
    width:100%;
    height:100%;
}
.songslist >ul > li span{
    position: absolute;
    top:2px;
    right:5px;
    color:white;
    padding-left:13px;
    font-size:12px;
    line-height:18px;
    background: transparent url(data:image/svg+xml;base64,) no-repeat 0 center;
    background-size:11px 10px;
}
.songslist >ul > li p{
    font-size:13px;
    padding:6px 2px 0 6px;
    line-height:15px;
    display: -webkit-box;
    -webkit-box-orient: vertical;
    -webkit-line-clamp: 2;
    overflow: hidden;
}
